import { Component, Input, OnDestroy } from '@angular/core';
import { FormGroup, Validators } from '@angular/forms';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { ImageUploaderComponent, ImageUploaderModel } from '@tei/material';
import { Subscription } from 'rxjs';
import { ReceiptSetupTextContent, ReceiptTypeEnum } from '../../@intermediate/print-receipt-setup/models/get-print-receipt-setup.model';
import { Language, PrintReceiptSetupForm } from '../../@intermediate/print-receipt-setup/models/print-receipt-setup.model';
@Component({
  selector: 'tei-shared-print-receipt-setup',
  templateUrl: './print-receipt-setup.component.html',
  styleUrls: ['./print-receipt-setup.component.scss']
})
export class PrintReceiptSetupComponent implements OnDestroy
{
  @Input() form: FormGroup<PrintReceiptSetupForm>;
  @Input() languages: Language[];
  @Input() receiptType!: ReceiptTypeEnum;
  LogoURl: string;
  subscription = new Subscription();
  textContent: ReceiptSetupTextContent;
  private readonly receiptSetupTextContent: Record<ReceiptTypeEnum, ReceiptSetupTextContent> = {
    [ReceiptTypeEnum.Receipt]: {
      Title: 'FBADMINISTRATION.PRINT_RECEIPT_SETUP.RECEIPT_SETUP',
      Description: 'FBADMINISTRATION.PRINT_RECEIPT_SETUP.RECEIPT_SETUP_DESCRIPTION'
    },
    [ReceiptTypeEnum.Approval]: {
      Title: 'FBADMINISTRATION.PRINT_APPROVAL_SLIP_SETUP.APPROVAL_SLIP_SETUP',
      Description: 'FBADMINISTRATION.PRINT_APPROVAL_SLIP_SETUP.APPROVAL_SLIP_DESCRIPTION'
    }
  };
  constructor(private dialog: MatDialog)
  {

  }

  ngOnInit(): void
  {
    this.textContent = this.receiptSetupTextContent[this.receiptType];
    this.LogoURl = this.form.value?.LogoUrl;
    this.subscription.add(this.form?.get('IncludeQRCode')?.valueChanges.subscribe((value) =>
    {
      const qrCodeContent = this.form.get('QRCodeContent');
      if (value)
      {
        qrCodeContent?.enable();
        qrCodeContent?.addValidators(Validators.required);
      }
      else
      {
        qrCodeContent?.disable();
        qrCodeContent?.removeValidators(Validators.required);
        qrCodeContent?.reset();
      }
      qrCodeContent?.updateValueAndValidity();
    }));

    this.subscription.add(this.form?.get('IncludeLogo')?.valueChanges.subscribe((value) =>
    {
      const logoUrl = this.form.get('LogoUrl');
      if (value)
      {
        logoUrl?.enable();
        logoUrl?.addValidators(Validators.required);
      }
      else
      {
        logoUrl?.disable();
        logoUrl?.clearValidators();
      }
      logoUrl?.updateValueAndValidity();
    }));
  }
  openProfilePhotoDialog(): void
  {
    if (this.form?.value?.IncludeLogo)
    {
      const formats = '.png,.jpg,.jpeg,.jpe,.jfif,.gif,.bmp,.tiff,.heic';
      const permittedFileTypes = ['JPG', 'JPEG', 'JPE', 'JFIF', 'PNG', 'GIF', 'BMP', 'TIFF', 'HEIC'];
      const data: ImageUploaderModel = {
        round: false,
        maintainAspectRatio: false,
        aspectRatio: 4 / 2.5,
        acceptFormat: formats,
        permittedFileTypes: permittedFileTypes,
        maxSize: 200,
        imageServerUrl: this.LogoURl,
      };
      const imageUploaderDialog = this.dialog.open(ImageUploaderComponent, {
        width: '400px',
        panelClass: ['o-dialog-md-fullscreen'],
        data,
      });


      imageUploaderDialog.afterClosed().subscribe((result) =>
      {
        if (result)
        {
          this.form.controls.LogoImage.setValue(result?.origImage);
          this.form.controls.LogoUrl.setValue(result?.croppedImage);
          this.LogoURl = result?.croppedImage;

          if (!result?.croppedImage)
          {
            this.form.controls.LogoImage.setValue(null);
            this.form.controls.LogoUrl.setValue(null);
            this.form.controls.LogoUID.setValue(null);
            this.LogoURl = null;
          }
        }
      });

    }
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }

}

