import { HttpClient } from "@angular/common/http";
import { Inject, Injectable } from "@angular/core";
import { IAPIBaseUrlProvider } from "@tei/core";
import { Observable } from "rxjs";
import { GetPrintReceiptSetupRequest, GetPrintReceiptSetupResponse } from "../@intermediate/print-receipt-setup/models/get-print-receipt-setup.model";
import { PostPrintReceiptSetupResponse } from "../@intermediate/print-receipt-setup/models/post-print-receipt-setup.model";
import { IPrintReceiptSetupService } from "../@intermediate/print-receipt-setup/print-receipt-setup.interface";

@Injectable()
export class PrintReceiptSetupService implements IPrintReceiptSetupService
{
  private readonly serviceUrl: string;

  constructor(@Inject("IAPIBaseUrlProvider") protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + "/FBAdministration/Terminal/ReceiptSetup";
  }
  Get(request: GetPrintReceiptSetupRequest): Observable<GetPrintReceiptSetupResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.get<GetPrintReceiptSetupResponse>(apiUrl, { params: request as any });
  }
  Post(request: FormData): Observable<PostPrintReceiptSetupResponse>
  {
    const apiUrl = this.serviceUrl;

    return this.http.post<PostPrintReceiptSetupResponse>(apiUrl, request);
  }


}

