import { Language } from "./print-receipt-setup.model";

export class GetPrintReceiptSetupRequest
{
  POSID: string;
  ReceiptType: ReceiptTypeEnum;
}

export enum ReceiptTypeEnum
{
  Receipt,
  Approval
}
export class GetPrintReceiptSetupResponse
{
  Languages: Language[];
  TerminalSetupReceipt: TerminalSetupReceipt;
}

export class ReceiptSetupTextContent
{
  Title: string;
  Description: string;
};
export class TerminalSetupReceipt
{
  POSID: string;
  Name: string;
  Address: string;
  City: string;
  State: string;
  Country: string;
  PostalCode: string;
  Phone: string;
  Header: string;
  Footer: string;
  ReceiptType: ReceiptTypeEnum;
  TerminalType: number;
  MemberIDCaption: string;
  AditionalTipCaption: string;
  RecomTipNewline: boolean;
  DisplayTotalFirstOnReceipt: true;
  LanguageCode: string;
  LanguageID: number;
  LanguageName: string;
  LanguageCulture: string;
  LanguageDescription: string;
  IncludeLogo: boolean;
  IncludeQRCode: boolean;
  LogoUID: string;
  LogoUrl: string;
  QRCodeContent: string;
}
